import {FetchSelf} from "@/services/Fetch.js"
 
export default {
    namespaced: true,
    state:{
        Username: null,
        ID: null,
        TeamSelection: []
    },
    getters:{
        FullProfile : state => {
            return { Username: state.Username, ID: state.ID }
        },
        Name: state => {
            return state.Username
        },
        ID: state => {
            return state.ID
        },
        TeamSelection: state => {
            return state.TeamSelection
        },
        TeamSelectionId: state => {
            return state.TeamSelection.map(function(char){
                return char.ID
            })
        }
    },
    mutations:{
        setUser(state, payload) {
            state.ID = payload.ID
            state.Username = payload.Username
        },
        addCharacter(state, payload) {
            state.TeamSelection.push(payload)
        },
        addToTeamSelection(state, char) {
            if (state.TeamSelection.length < 3) {
                state.TeamSelection.push(char)
            }
        }
    },
    actions:{
        setUser(context) {
            FetchSelf()
            .then(res => {
                context.commit("setUser", res.data)
            })
            .catch(err => {
                return err
            })
            
        },
        addToTeamSelection(context, char) {
            context.commit("addToTeamSelection", char)
        }
    },
}