export default {
    namespaced: true,
    state: {
        connected: false,
        socket: null
    },
    getters: {
        socket: state => {
            if (state.connected) {
                return state.socket
            }
        }
    },
    mutations: {
        connect(state, id) {

            state.socket = new WebSocket("ws://localhost:3000/arena/ws/" + id)
            state.connected = true

            state.socket.onmessage = function (evt) {
                const res = JSON.parse(evt.data)
                if (res.code === 0) {
                    let date = new Date();
                    date.setTime(date.getTime() + (5 * 60 * 1000));
                    const expires = "; expires=" + date.toUTCString();
                    document.cookie = "gid=" + (res.gid || "") + expires + "; path=/";
                }
            }

        }
    },
    actions: {
        connect(context, id) {
            context.commit("connect", id)
        }
    }
}